.signup-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* justify-content: space-around; */
    width: 60vw;
    margin-left: 20vw;
    font-family: 'Recoleta-Medium';
    font-color: #4a4a4a;
}

.signup-input {
    height: 6vh;
    /* width: 300px */
    /* width: 75%; */
    /* flex: 3 1 200px */
    flex: 2 1 60%;
    /* font-size: 20px;  */
    border-radius: 10px;
    /* border: solid gray 1px; */
    border: none;
    padding-left: 1vw;
    font-size: 2vw;
    box-shadow: -2px 4px 10px 0px rgba(0,0,0,0.75);
    /* outline: none; */
}

.signup-btn {
    height: 6.6vh;
    /* width: 100px; */
    /* width: 25%; */
    /* flex: 1 1 200px */
    flex: 1 1 35%;
    margin-left: 1.5%;
    /* font-size: 20px;   */
    border-radius: 10px;
    border: none;
    background-color: #ea4756;
    font-family: 'Recoleta-Bold';
    font-size: 2.5vw;
    cursor: pointer;
    box-shadow: -0.5px 1px 10px 0px rgba(0,0,0,0.75);

	border: 1px solid rgba(0,0,0,0.21);
	border-bottom: 4px solid rgba(0,0,0,0.21);
	border-radius: 4px;
	text-shadow: 0 1px 0 rgba(0,0,0,0.15);

    /* background: rgba(250,90,90,1); */
    background: #ea4756;
	background: -webkit-gradient(linear, 0 0, 0 100%, from(#ea4756), to(rgba(232,81,81,1)));
	background: -webkit-linear-gradient(#ea4756 0%, rgba(232,81,81,1) 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(#ea4756), to(rgba(232,81,81,1)));
	background: linear-gradient(#ea4756 0%, rgba(232,81,81,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fa5a5a', endColorstr='#e85151', GradientType=0 );
}

